<template>
 <div class="lucky">
   <LuckyWheel
     ref="myLucky"
     width="300px"
     height="300px"
     :prizes="prizes"
     :blocks="blocks"
     :buttons="buttons"
     @start="startCallback"
     @end="endCallback"
     style="margin: auto"
 />
 </div>
</template>

<script>
export default {
  // eslint-disable-next-line vue/multi-word-component-names
  name: 'lucky',
  data () {
    return {
      blocks: [
        { padding: '10px', background: '#869cfa' }
      ],
      prizes: [
        {background: '#a6a3ee', fonts: [{text: '唱歌', top: 30}], },
        {background: '#f6e1c5', fonts: [{text: '看日出', top: 30}], },
        {background: '#e9e8fe', fonts: [{text: '谢谢惠顾', top: 30}], },
      ],
      buttons: [
        { radius: '40%', background: '#617df2' },
        { radius: '35%', background: '#afc8ff' },
        {
          radius: '30%', background: '#869cfa',
          pointer: true,
          fonts: [{ text: '开始', top: '-10px' }]
        }
      ]
    }
  },
  methods: {
    // 点击抽奖按钮会触发star回调
    startCallback () {
      // 调用抽奖组件的play方法开始游戏
      this.$refs.myLucky.play()
      // 模拟调用接口异步抽奖
      setTimeout(() => {
        // // 假设后端返回的中奖索引是0
        // const index = 0
        // // 调用stop停止旋转并传递中奖索引
        const value= Math.floor(Math.random() * (100 - 1)) + 1;
        console.log(value)
        alert(value)
        if (value<=90){
          this.$refs.myLucky.stop(0)
        }
        if (value>90&&value<=99){
          this.$refs.myLucky.stop(1)
        }
        if (value==100){
          this.$refs.myLucky.stop(2)
        }
      }, 3000)
    },
    // 抽奖结束会触发end回调
    endCallback (prize) {
      console.log(prize.fonts[0].text)
      alert(prize.fonts[0].text)
    },
  }
}
</script>

<style scoped>
.lucky{
  width: 100%;
}
</style>